                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on">
                          Import<v-icon x-small right dark>mdi-import</v-icon>
                        </v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click=" dialogImportExcel = true" >Excel</v-list-item-title >
                        </v-list-item>
                      </v-list>
                    </v-menu >&nbsp;
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;
                    <v-btn @click="openWhoIsDialog"  x-small color="red" class="white--text" > Who Is </v-btn>&nbsp;

                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:item.device_name="{ item }">
                <a @click="onClickOpen(item)"> {{ item.device_name }} </a>
              </template>
              <template v-slot:item.device_enable="{ item }" >
                <v-checkbox v-model="item.device_enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
                <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
              </template>
            
              <template v-slot:header.device_enable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('device_enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              
              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.network_name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_name" label="device name" required :rules="[() => !isExistingvalue(editedItem.device_name, 'device_name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.device_enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.type_mstp" label="type mstp"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_rpm" label="supports rpm"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_wpm" label="supports wpm"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_ip" label="device ip" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_port" label="port" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_mask" label="mask" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_mac" label="mac" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_object_id" label="id" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.network_number" label="network number" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="device_uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.device_uuid, 'device_uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.device_uuid, 'device_uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createDevice(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.device_uuid, 'device_uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.device_name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.device_uuid, 'device_uuid')" v-model="item.device_name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.network_name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.network_name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>

    <!-- who is dialog-->
    <v-dialog v-model="whoIsDialog">
      <v-card >
        <v-card-title>
          <span class="headline">Who Is</span>
        </v-card-title>
        <v-card-text class="text-start">
          <v-container>
            <v-row style="padding: 0 24px;">
              <v-col cols="12" sm="6" md="3">
                <v-text-field v-model="user_options.network_number" label="network number" type="number"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-text-field v-model="user_options.range_start" label="range start" type="number"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-text-field v-model="user_options.range_end" label="range end" type="number"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.is_mstp" label="is mstp"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.whois" label="who is"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.global_broadcast" label="global broadcast"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.full_range" label="full range"></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.add_devices" label="add devices"></v-checkbox>
              </v-col>
            </v-row>
            <v-expansion-panels :value="panel" multiple>
              <v-expansion-panel v-for="(key, index) in Object.keys(whoIsResult)" :key="index" >
                <v-expansion-panel-header>
                  <h3>{{toUpperTextKey(key)}}</h3>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-data-table
                    :headers="key == 'discovered_devices' ? discoveredDeviceHeaders : addedDeviceHeaders"
                    :items="whoIsResult[key]"
                    :hide-default-footer="true"
                    class="elevation-1"
                  ></v-data-table>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="whoIsDialog = false">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveWhoIs">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import BmRightSidebar from './BmRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import * as changeCase from "change-case";
import { DATA_TABLE_PROPS } from '../../constants/appConstant';

export default {
  components: {
    MqttConsole,
    BmRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    valid: false,
    dialog: false,
    dialogDelete: false,
    deviceHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'device_name', align: 'start' },
      { text: 'Enable', value: 'device_enable', sortable: false },
      { text: 'IP', value: 'device_ip', sortable: false },
      { text: 'Mask', value: 'device_mask', sortable: false },
      { text: 'Port', value: 'device_port', sortable: false },
      { text: 'Mac', value: 'device_mac', sortable: false },
      { text: 'Id', value: 'device_object_id', sortable: false },
      { text: 'Type MSTP', value: 'type_mstp', sortable: false },
      { text: 'Supports RPM', value: 'supports_rpm', sortable: false },
      { text: 'Supports WPM', value: 'supports_wpm', sortable: false },
      { text: 'Network UUID', value: 'network_uuid', sortable: false },
      { text: 'Network Number', value: 'network_number', sortable: false },
      { text: 'UUID', value: 'device_uuid', sortable: false },
    ],
    editedIndex: -1,
    editedItem: { 
      device_name: 'Master',
      device_ip: '192.168.15.10',
      device_mask: 24,
      device_port: 47808,
      device_mac: 0,
      device_object_id: 0,
      network_number: 0,
      type_mstp: false,
      supports_rpm: false,
      supports_wpm: false,
      device_enable: true,
    },
    defaultItem: { 
      device_name: 'Master',
      device_ip: '192.168.15.10',
      device_mask: 24,
      device_port: 47808,
      device_mac: 0,
      device_object_id: 0,
      network_number: 0,
      type_mstp: false,
      supports_rpm: false,
      supports_wpm: false,
      device_enable: true,
    },
    currentItem: {},
    dataImport: '',
    rules: { required: value => !!value || 'Required.' },
    network: {},
    selected: [],
    action: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    allPoints: [],
    dialogImportExcel: false,
    search: '',
    searchWizard: '',
    dialogMassEdit: false,
    editAll: {
      device_enable: false,
    },
    editField: "",
    intervalId: null,
    isClickSaveImport: false,
    dirs: [],
    tableProps: {...DATA_TABLE_PROPS},
    whoIsDialog: false,
    whoIsResult: {},
    discoveredDeviceHeaders: [
      { text: 'Name', value: 'device_name', align: 'start' },
      { text: 'IP', value: 'device_ip', sortable: false },
      { text: 'Mac', value: 'device_mac', sortable: false },
      { text: 'Id', value: 'device_object_id', sortable: false },
      { text: 'Type MSTP', value: 'type_mstp', sortable: false },
      { text: 'Supports RPM', value: 'supports_rpm', sortable: false },
      { text: 'Supports WPM', value: 'supports_wpm', sortable: false },
      { text: 'Network Number', value: 'network_number', sortable: false },
      { text: 'Vendor Name', value: 'vendor_name', sortable: false },
    ],
    addedDeviceHeaders: [
      { text: 'Name', value: 'device_name', align: 'start' },
      { text: 'IP', value: 'device_ip', sortable: false },
      { text: 'Mask', value: 'device_mask', sortable: false },
      { text: 'Port', value: 'device_port', sortable: false },
      { text: 'Mac', value: 'device_mac', sortable: false },
      { text: 'Id', value: 'device_object_id', sortable: false },
      { text: 'Type MSTP', value: 'type_mstp', sortable: false },
      { text: 'Supports RPM', value: 'supports_rpm', sortable: false },
      { text: 'Supports WPM', value: 'supports_wpm', sortable: false },
      { text: 'Network UUID', value: 'network_uuid', sortable: false },
      { text: 'Network Number', value: 'network_number', sortable: false },
    ],
    panel: [],
    user_options: {},
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    networksData () {
      const { bmNetworks } = this.$store.state;
      return bmNetworks;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getData();
    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.network = await axios.get(`bacnet_master/network/${this.$route.params.id}`);
      this.getDirs();
      this.editedItem.network_uuid = this.defaultItem.network_uuid = this.network.network_uuid;
      //update BmNetworksData
      this.updateBmNetworksData(this.network);
    },
    getDirs(){
      this.dirs = [{text: "NETWORKS", link: '/bacnet-master/networks'}];
      this.dirs.push({text: `NET: ${this.network.network_name}`});
    },
    updateBmNetworksData(newNetwork){
      const indexNetWork = this.networksData.findIndex(nw=>nw.network_uuid == newNetwork.network_uuid);
      if(indexNetWork > -1){
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeBmNetworksData', this.networksData);
      }
    },
    onClickOpen(item) {
      this.$router.push({ path: `/bacnet-master/devices/${item.device_uuid}/points` });
    },
    editItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateDevice(this.editedItem);
        } else {
          this.createDevice(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.currentItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/device/${this.currentItem.device_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.network_uuid = this.network.network_uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try{
            const promises = [];
            for (const item of this.importArray) {
              item.network_uuid = this.network.network_uuid;  //update to the current network_uuid
              if(!item.device_uuid){
                promises.push(this.createDevice(item));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(device=>{
        delete device.device_uuid;
        return device;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const devices = _cloneDeep(this.network.devices);
      if (this.editedIndex !== -1) {
        //when edit
        devices.splice(this.editedIndex, 1);
      }
      if (fieldName === 'address' || fieldName === 'tcp_port') {
        value = Number(value);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    async clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertToExportData(array);
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(this.network.devices));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            device.points.forEach((point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                const pointKey = `point[${indexP}].${point_key}`;
                device = { ...device, [pointKey]: point_value };
              });
            });
            delete device.points;
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      axios.patch(`bacnet_master/device/${data.device_uuid}`, data).then(response => {
        const index = this.network.devices.findIndex(d => d.device_uuid == response.device_uuid);
        Object.assign(this.network.devices[index], response);
        this.close();
        return this.$toasted.show('update success');
      });
    },
    async createDevice(data) {
      const response =  await axios.put(`bacnet_master/device`, data);
      this.network.devices.push(response);
      if (this.importArray && this.importArray.length > 0) {
        const index = this.importArray.findIndex(n => n.network_name == response.network_name);
        if(index > -1){
          Object.assign(this.importArray[index], response);
        }
      }
      this.close();       
      this.$toasted.show('create success');
      if (data.points && data.points.length > 0) {
        for (const point of data.points) {
          point.device_uuid = response.device_uuid;
          this.createPoint(point);
        }
      }                   
    },
    async createPoint(data) {
      axios.post(`bacnet_master/point`, data).then(response => {
      });
    },

    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        device_enable: false,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    async saveWhoIs(){
        const result = await axios.post(`bacnet_master/b/network/whois/${this.network.network_uuid}`, this.user_options);
        if(!result) return this.$toasted.show('fail!', { type: 'error' });
        this.whoIsResult = {};
        Object.keys(result).forEach(key=>{
          if(typeof result[key] === 'object' && result[key] !== null){
            const devices = Object.keys(result[key]).map(subKey=>{ return result[key][subKey]; });
            this.whoIsResult = {...this.whoIsResult, [key]: devices};
          }
        });
        this.panel = Object.keys(this.whoIsResult).map((k, i) => i);
        this.$toasted.show('success!', { type: 'success' });
    },
    openWhoIsDialog(){
      this.whoIsDialog = true;
      this.whoIsResult = {};
      this.user_options= {
      "network_number": 0,
      "is_mstp": false,
      "whois": true,
      "global_broadcast": true,
      "full_range": true,
      "range_start": 1,
      "range_end": 202,
      "add_devices": true,
      "show_supported_services":false
      }
    },

  },
};
</script>
